* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
}

.App {
  height: 100vh;
  min-height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}

button {
  font-family: inherit;
}

input {
  font-family: inherit;
}

select {
  font-family: inherit;
}

/* Hide scrollbars for category navigation */
.category-scroll::-webkit-scrollbar {
  display: none;
}

.category-scroll {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

/* Bracelet positioning styles */
.bracelet-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.stone {
  position: absolute;
  transition: all 0.2s ease;
  user-select: none;
}

.stone.has-letter {
  box-shadow: 0 2px 8px rgba(0,0,0,0.15);
}

.stone.has-charm {
  box-shadow: 0 2px 8px rgba(255,215,0,0.3);
  animation: charmGlow 2s ease-in-out infinite alternate;
}

.stone:not(.has-letter):not(.has-charm) {
  opacity: 0.7;
}

.stone:not(.has-letter):not(.has-charm):hover {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1.2);
}

img {
  max-width: 100%;
  height: auto;
}

/* Charm glow animation */
@keyframes charmGlow {
  from {
    box-shadow: 0 2px 8px rgba(255,215,0,0.3);
  }
  to {
    box-shadow: 0 4px 12px rgba(255,215,0,0.5);
  }
}

/* Dropzone highlighting */
.stone.dropzone-active {
  border: 2px solid #4F46E5 !important;
  background: rgba(79, 70, 229, 0.1) !important;
}

.stone.dropzone-hover {
  border: 2px solid #10B981 !important;
  background: rgba(16, 185, 129, 0.1) !important;
  transform: translate(-50%, -50%) scale(1.1);
}

/* Bracelet Preview Container - matching Little Words Project structure */
.bracelet-preview-container {
  width: 450px;
  height: 450px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
  margin: 0 auto 20px;
  position: relative;
}

/* Product Canvas - main container */
.product-canvas {
  width: 100%;
  height: 100%;
  position: relative;
}

/* Product Overlapping - contains bracelet and letters */
.product-overlapping {
  position: relative;
  width: 100%;
  height: 100%;
}

/* Main Bracelet Image */
.main-bracelet-image {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
}

/* Product Overlapping Main Charm - charm overlay */
._productOverlappingMainCharm_c850n_13 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
}

/* Product Overlapping Content - container for letters */
.product-overlapping-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}

/* Product Overlapping Letter - individual letter container */
.product-overlapping-letter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* Remove flex centering - let the pre-rendered images handle their own positioning */
}

/* Responsive Design */
@media (min-width: 429px) {
  .bracelet-preview-container {
    width: 590px;
    height: 590px;
    max-width: 590px;
    max-height: 590px;
  }
}

@media (max-width: 1024px) {
  .App > div {
    grid-template-columns: 1fr !important;
    gap: 30px !important;
  }
}

@media (max-width: 768px) {
  .App > div {
    padding: 15px !important;
    gap: 20px !important;
  }
}

/* Charm Dropzones */
._productOverlappingDropzone_c850n_42 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 15;
  pointer-events: none;
}

._dropzone_1xii1_43 {
  position: absolute;
  height: 2rem;
  width: 2rem;
  border-radius: 9999px;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  --tw-bg-opacity: .3;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: .3s;
  box-shadow: 0 0 10px 4px #da9fc466;
  -webkit-backdrop-filter: blur(.25px);
  backdrop-filter: blur(.25px);
  pointer-events: all;
}

@media (min-width: 1280px) {
  ._dropzone_1xii1_43 {
    height: 3rem;
    width: 3rem;
  }
}

._magneticZone_1xii1_5 {
  position: absolute;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 250%;
  height: 150%;
  pointer-events: none;
}

/* Loading spinner animation */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Modal-specific styles */
.bracelet-modal {
  display: none;
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
}

.bracelet-modal.active {
  display: flex;
  align-items: center;
  justify-content: center;
}

.bracelet-modal-content {
  background-color: white;
  width: 90vw;
  max-width: 1200px;
  height: 85vh;
  max-height: 700px;
  border-radius: 12px;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.bracelet-modal-close {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 28px;
  font-weight: bold;
  color: #666;
  cursor: pointer;
  z-index: 10000;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e5e7eb;
}

.bracelet-modal-close:hover {
  color: #333;
  background: white;
}

/* Adjust App layout when in modal */
.bracelet-modal .App {
  height: 100%;
  min-height: unset;
}

.bracelet-modal .App > div {
  height: 100%;
  min-height: unset;
}

/* Adjust options panel when in modal */
.bracelet-modal .App > div > div:last-child {
  min-height: 100%;
  max-height: 100%;
}